`, "",{"version":3,"sources":["webpack://./src/网红小区vue版/components/ToggleButton.vue"],"names":[],"mappings":"AA+FA,iCACE,yBAA0B,CAC1B,UAAW,CACX,WAAY,CACZ,sCAAuC,CACvC,iBAAkB,CAClB,YAAa,CACb,kBAAmB,CACnB,sBAAuB,CACvB,WAAY,CACZ,sCAAuC,CACvC,+BAAgC,CAChC,gBAAiB,CACjB,wBAAyB,CACzB,iBAAkB,CAClB,cAAe,CACf,sCAA0C,CAC1C,UAAW,CACX,8HAGF,CAEA,uCACE,qBACF,CAEA,0CACE,0BAA2B,CAC3B,WAAY,CACZ,+CAAgD,CAChD,0BACF","sourcesContent":["<template>\n  <div\n    ref=\"buttonRef\"\n    class=\"ngq-toggle-btn\"\n    :class=\"{ dragging: isDragging }\"\n    :style=\"buttonStyle\"\n    :title=\"tooltipText\"\n  >\n    🏢\n  </div>\n</template>\n\n<script setup lang=\"ts\">\nimport { ref, computed, onMounted } from 'vue';\nimport { useUIStore } from '../stores/ui';\nimport { useDrag } from '../composables/useDrag';\nimport { usePosition } from '../composables/usePosition';\n\n// Stores\nconst uiStore = useUIStore();\n\n// 元素引用\nconst buttonRef = ref<HTMLElement | null>(null);\n\n// 位置管理\nconst { position, savePosition, restorePosition } = usePosition({\n  key: 'button',\n  defaultPosition: { left: 20, top: 100 },\n  autoSave: false, // 手动保存，在拖动结束时\n});\n\n// 拖动功能\nconst { isDragging } = useDrag({\n  elementRef: buttonRef,\n  enabled: true,\n  constrainToViewport: true,\n  boundaryOffset: 10,\n  clickThreshold: 5,\n  onDragStart: (pos) => {\n    console.log('🖱️ 开始拖动按钮', pos);\n  },\n  onDragMove: (pos) => {\n    // 实时更新位置（但不保存）\n    position.value = pos;\n  },\n  onDragEnd: (pos, isClick) => {\n    console.log('✅ 拖动结束', { position: pos, isClick });\n    \n    // 保存位置\n    savePosition(pos);\n    \n    // 如果是点击，切换面板\n    if (isClick) {\n      handleClick();\n    }\n  },\n});\n\n/**\n * 按钮样式\n */\nconst buttonStyle = computed(() => ({\n  left: `${position.value.left}px`,\n  top: `${position.value.top}px`,\n}));\n\n/**\n * 提示文本\n */\nconst tooltipText = computed(() => {\n  return uiStore.isPanelVisible ? '点击关闭面板' : '点击打开面板';\n});\n\n/**\n * 处理点击\n */\nconst handleClick = () => {\n  console.log('🎨 检测到点击，切换面板');\n  uiStore.togglePanel();\n  \n  // 如果打开面板，可以触发数据加载\n  // 这里暂时不加载，等主面板组件创建后再处理\n};\n\n/**\n * 初始化\n */\nonMounted(() => {\n  // 恢复保存的位置\n  const savedPos = restorePosition();\n  console.log('✅ ToggleButton 已挂载，位置:', savedPos);\n});\n</script>\n\n<style scoped>\n.ngq-toggle-btn {\n  position: fixed !important;\n  width: 64px;\n  height: 64px;\n  background: var(--apt-gradient-primary);\n  border-radius: 50%;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  cursor: grab;\n  z-index: var(--apt-z-button) !important;\n  box-shadow: var(--apt-shadow-md);\n  user-select: none;\n  -webkit-user-select: none;\n  touch-action: none;\n  font-size: 28px;\n  border: 3px solid rgba(255, 255, 255, 0.3);\n  color: #fff;\n  transition: transform var(--apt-transition-normal), \n              box-shadow var(--apt-transition-normal), \n              opacity var(--apt-transition-normal);\n}\n\n.ngq-toggle-btn:hover {\n  transform: scale(1.05);\n}\n\n.ngq-toggle-btn.dragging {\n  cursor: grabbing !important;\n  opacity: 0.9;\n  z-index: var(--apt-z-button-dragging) !important;\n  transition: none !important;\n}\n</style>\n\n"],"sourceRoot":""}]);
