hB,eAAiB,CACjB,2CAA6C,CAC7C,gBAAkB,CAClB,wBACD,CAED,eACE,0BAA4B,CAC5B,+BACD,CAED,gBACE,uBACD,CAED,kBACE,WAAa,CACb,kBACD,CAED,mBACE,gCAAqC,CACrC,kCACD,CAED,yBACE,iCACD,CAED,eACE,gBAAkB,CAClB,cACD,CAED,eACE,iBAAmB,CACnB,cACD,CAID,WACE,UAAY,CACZ,iBAAmB,CACnB,iCAAsC,CACtC,kCAAoC,CACpC,kCAAoC,CACpC,qBAAuB,CACvB,cAAgB,CAChB,2CACD,CAED,iBACE,YAAc,CACd,+BAAiC,CACjC,iCACD,CAED,wBACE,2BACD,CAID,WACE,YAAc,CACd,qBAAuB,CACvB,kBAAoB,CACpB,sBAAwB,CACxB,iBAAmB,CACnB,oBACD,CAED,gBACE,cAAgB,CAChB,kBAAoB,CACpB,WACD,CAED,gBACE,cAAgB,CAChB,WACD,CAID,aACE,YAAc,CACd,qBAAuB,CACvB,kBAAoB,CACpB,sBAAwB,CACxB,iBAAmB,CACnB,qBACD,CAED,kBACE,cAAgB,CAChB,kBAAoB,CACpB,iCACD,CAED,kBACE,cAAgB,CAChB,WACD","sourcesContent":[null,"/**\n * CSS 变量定义\n * 网红小区插件的全局样式变量\n */\n\n:root {\n  /* 主色调 */\n  --apt-primary: #ff6b9d;\n  --apt-secondary: #c44569;\n  \n  /* 背景色 */\n  --apt-bg: #1a1a2e;\n  --apt-card: #16213e;\n  \n  /* 边框色 */\n  --apt-border: rgba(255, 107, 157, 0.2);\n  \n  /* 文字色 */\n  --apt-text: #e8eaf6;\n  --apt-dim: #e5e7eb;\n  \n  /* 阴影 */\n  --apt-shadow-sm: 0 2px 8px rgba(0, 0, 0, 0.2);\n  --apt-shadow-md: 0 6px 20px rgba(255, 107, 157, 0.4);\n  --apt-shadow-lg: 0 8px 32px rgba(0, 0, 0, 0.4);\n  \n  /* 渐变 */\n  --apt-gradient-primary: linear-gradient(135deg, #ff6b9d 0%, #c44569 100%);\n  --apt-gradient-bg: linear-gradient(135deg, #1a1a2e 0%, #16213e 100%);\n  \n  /* 圆角 */\n  --apt-radius-sm: 8px;\n  --apt-radius-md: 12px;\n  --apt-radius-lg: 16px;\n  --apt-radius-full: 50%;\n  \n  /* 间距 */\n  --apt-spacing-xs: 4px;\n  --apt-spacing-sm: 8px;\n  --apt-spacing-md: 12px;\n  --apt-spacing-lg: 16px;\n  --apt-spacing-xl: 20px;\n  \n  /* 过渡 */\n  --apt-transition-fast: 0.15s ease;\n  --apt-transition-normal: 0.2s ease;\n  --apt-transition-slow: 0.3s ease;\n  \n  /* Z-index */\n  --apt-z-button: 10000;\n  --apt-z-button-dragging: 10001;\n  --apt-z-panel: 9999;\n  --apt-z-modal: 10002;\n}\n\n","/**\n * 动画定义\n * 网红小区插件的动画效果\n */\n\n/* 旋转动画（用于加载图标） */\n@keyframes spin {\n  from {\n    transform: rotate(0deg);\n  }\n  to {\n    transform: rotate(360deg);\n  }\n}\n\n/* 淡入动画 */\n@keyframes fadeIn {\n  from {\n    opacity: 0;\n  }\n  to {\n    opacity: 1;\n  }\n}\n\n/* 淡出动画 */\n@keyframes fadeOut {\n  from {\n    opacity: 1;\n  }\n  to {\n    opacity: 0;\n  }\n}\n\n/* 从上滑入 */\n@keyframes slideInDown {\n  from {\n    transform: translateY(-20px);\n    opacity: 0;\n  }\n  to {\n    transform: translateY(0);\n    opacity: 1;\n  }\n}\n\n/* 从下滑入 */\n@keyframes slideInUp {\n  from {\n    transform: translateY(20px);\n    opacity: 0;\n  }\n  to {\n    transform: translateY(0);\n    opacity: 1;\n  }\n}\n\n/* 缩放进入 */\n@keyframes scaleIn {\n  from {\n    transform: scale(0.9);\n    opacity: 0;\n  }\n  to {\n    transform: scale(1);\n    opacity: 1;\n  }\n}\n\n/* 缩放退出 */\n@keyframes scaleOut {\n  from {\n    transform: scale(1);\n    opacity: 1;\n  }\n  to {\n    transform: scale(0.9);\n    opacity: 0;\n  }\n}\n\n/* 脉冲动画 */\n@keyframes pulse {\n  0%, 100% {\n    opacity: 1;\n  }\n  50% {\n    opacity: 0.5;\n  }\n}\n\n/* 工具类 */\n.animate-spin {\n  animation: spin 1s linear infinite;\n}\n\n.animate-fade-in {\n  animation: fadeIn 0.3s ease;\n}\n\n.animate-fade-out {\n  animation: fadeOut 0.3s ease;\n}\n\n.animate-slide-in-down {\n  animation: slideInDown 0.3s ease;\n}\n\n.animate-slide-in-up {\n  animation: slideInUp 0.3s ease;\n}\n\n.animate-scale-in {\n  animation: scaleIn 0.2s ease;\n}\n\n.animate-scale-out {\n  animation: scaleOut 0.2s ease;\n}\n\n.animate-pulse {\n  animation: pulse 2s ease-in-out infinite;\n}\n\n","/**\n * 通用样式\n * 网红小区插件的全局通用样式\n */\n\n/* 导入变量和动画 */\n@import './variables.css';\n@import './animations.css';\n\n/* ==================== 全局样式重置 ====================*/\n\n* {\n  box-sizing: border-box;\n}\n\n/* ==================== 工具类 ====================*/\n\n/* 用户选择控制 */\n.user-select-none {\n  user-select: none;\n  -webkit-user-select: none;\n}\n\n/* 触摸操作控制 */\n.touch-action-none {\n  touch-action: none;\n}\n\n/* Flex 布局 */\n.flex {\n  display: flex;\n}\n\n.flex-col {\n  display: flex;\n  flex-direction: column;\n}\n\n.items-center {\n  align-items: center;\n}\n\n.justify-center {\n  justify-content: center;\n}\n\n.justify-between {\n  justify-content: space-between;\n}\n\n.flex-1 {\n  flex: 1;\n}\n\n.flex-shrink-0 {\n  flex-shrink: 0;\n}\n\n/* 文本对齐 */\n.text-center {\n  text-align: center;\n}\n\n.text-left {\n  text-align: left;\n}\n\n.text-right {\n  text-align: right;\n}\n\n/* 文本样式 */\n.font-bold {\n  font-weight: 600;\n}\n\n.text-sm {\n  font-size: 12px;\n}\n\n.text-base {\n  font-size: 14px;\n}\n\n.text-lg {\n  font-size: 16px;\n}\n\n/* 间距 */\n.gap-xs {\n  gap: var(--apt-spacing-xs);\n}\n\n.gap-sm {\n  gap: var(--apt-spacing-sm);\n}\n\n.gap-md {\n  gap: var(--apt-spacing-md);\n}\n\n.gap-lg {\n  gap: var(--apt-spacing-lg);\n}\n\n/* 圆角 */\n.rounded-sm {\n  border-radius: var(--apt-radius-sm);\n}\n\n.rounded-md {\n  border-radius: var(--apt-radius-md);\n}\n\n.rounded-lg {\n  border-radius: var(--apt-radius-lg);\n}\n\n.rounded-full {\n  border-radius: var(--apt-radius-full);\n}\n\n/* 阴影 */\n.shadow-sm {\n  box-shadow: var(--apt-shadow-sm);\n}\n\n.shadow-md {\n  box-shadow: var(--apt-shadow-md);\n}\n\n.shadow-lg {\n  box-shadow: var(--apt-shadow-lg);\n}\n\n/* 过渡 */\n.transition {\n  transition: all var(--apt-transition-normal);\n}\n\n.transition-fast {\n  transition: all var(--apt-transition-fast);\n}\n\n.transition-slow {\n  transition: all var(--apt-transition-slow);\n}\n\n.transition-none {\n  transition: none !important;\n}\n\n/* 光标 */\n.cursor-pointer {\n  cursor: pointer;\n}\n\n.cursor-grab {\n  cursor: grab;\n}\n\n.cursor-grabbing {\n  cursor: grabbing;\n}\n\n/* 溢出控制 */\n.overflow-hidden {\n  overflow: hidden;\n}\n\n.overflow-auto {\n  overflow: auto;\n}\n\n.overflow-y-auto {\n  overflow-y: auto;\n}\n\n/* 滚动条样式 */\n.custom-scrollbar::-webkit-scrollbar {\n  width: 8px;\n  height: 8px;\n}\n\n.custom-scrollbar::-webkit-scrollbar-track {\n  background: rgba(255, 255, 255, 0.05);\n  border-radius: 4px;\n}\n\n.custom-scrollbar::-webkit-scrollbar-thumb {\n  background: var(--apt-primary);\n  border-radius: 4px;\n}\n\n.custom-scrollbar::-webkit-scrollbar-thumb:hover {\n  background: var(--apt-secondary);\n}\n\n/* ==================== 按钮样式 ====================*/\n\n.ngq-btn {\n  padding: 10px 20px;\n  background: var(--apt-gradient-primary);\n  color: white;\n  border: none;\n  border-radius: var(--apt-radius-sm);\n  cursor: pointer;\n  font-size: 14px;\n  font-weight: 500;\n  transition: all var(--apt-transition-normal);\n  user-select: none;\n  -webkit-user-select: none;\n}\n\n.ngq-btn:hover {\n  transform: translateY(-2px);\n  box-shadow: var(--apt-shadow-md);\n}\n\n.ngq-btn:active {\n  transform: translateY(0);\n}\n\n.ngq-btn:disabled {\n  opacity: 0.5;\n  cursor: not-allowed;\n}\n\n.ngq-btn-secondary {\n  background: rgba(255, 255, 255, 0.1);\n  border: 1px solid var(--apt-border);\n}\n\n.ngq-btn-secondary:hover {\n  background: rgba(255, 255, 255, 0.15);\n}\n\n.ngq-btn-small {\n  padding: 6px 12px;\n  font-size: 12px;\n}\n\n.ngq-btn-large {\n  padding: 14px 28px;\n  font-size: 16px;\n}\n\n/* ==================== 输入框样式 ====================*/\n\n.ngq-input {\n  width: 100%;\n  padding: 10px 12px;\n  background: rgba(255, 255, 255, 0.05);\n  border: 1px solid var(--apt-border);\n  border-radius: var(--apt-radius-sm);\n  color: var(--apt-text);\n  font-size: 14px;\n  transition: all var(--apt-transition-normal);\n}\n\n.ngq-input:focus {\n  outline: none;\n  border-color: var(--apt-primary);\n  background: rgba(255, 255, 255, 0.08);\n}\n\n.ngq-input::placeholder {\n  color: rgba(255, 255, 255, 0.3);\n}\n\n/* ==================== 空状态 ====================*/\n\n.ngq-empty {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  padding: 40px 20px;\n  color: var(--apt-dim);\n}\n\n.ngq-empty-icon {\n  font-size: 48px;\n  margin-bottom: 16px;\n  opacity: 0.5;\n}\n\n.ngq-empty-text {\n  font-size: 14px;\n  opacity: 0.7;\n}\n\n/* ==================== 加载状态 ====================*/\n\n.ngq-loading {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  padding: 40px 20px;\n  color: var(--apt-text);\n}\n\n.ngq-loading-icon {\n  font-size: 32px;\n  margin-bottom: 12px;\n  animation: spin 1s linear infinite;\n}\n\n.ngq-loading-text {\n  font-size: 14px;\n  opacity: 0.7;\n}\n\n"],"sourceRoot":""}]);
