`, "",{"version":3,"sources":["webpack://./src/网红小区vue版/App.vue"],"names":[],"mappings":"AAgGA,8BACE,iBACF,CAGA,kCACE,cAAe,CACf,OAAQ,CACR,QAAS,CACT,8BAAgC,CAChC,WAAY,CACZ,cAAe,CACf,wBAAyB,CACzB,mCAAoC,CACpC,kCAAmC,CACnC,YAAa,CACb,aAAc,CACd,qBAAsB,CACtB,+BAAgC,CAChC,mDACF,CAEA,qCACE,iBAAkB,CAClB,wBAAyB,CACzB,cACF,CAEA,qCACE,mBAAoB,CACpB,qBAAsB,CACtB,cAAe,CACf,eACF,CAEA,gCACE,kBAAmB,CACnB,YAAa,CACb,iCAAqC,CACrC,kCACF,CAEA,kCACE,YAAa,CACb,cAAe,CACf,oBACF,CAEA,uCACE,oBACF,CAGA,mCACE,cAAe,CACf,WAAY,CACZ,UAAW,CACX,UAAW,CACX,WAAY,CACZ,sCAAuC,CACvC,sCAA0C,CAC1C,iBAAkB,CAClB,cAAe,CACf,cAAe,CACf,aAAc,CACd,+BAAgC,CAChC,2CAA4C,CAC5C,YAAa,CACb,kBAAmB,CACnB,sBACF,CAEA,yCACE,oBAAqB,CACrB,+BACF","sourcesContent":["<template>\n  <div class=\"ngq-vue-app\">\n    <!-- 拖动按钮 -->\n    <ToggleButton />\n\n    <!-- 测试信息面板（开发时显示） -->\n    <div v-if=\"showDebugPanel\" class=\"ngq-debug-panel\">\n      <h3>🧪 测试面板</h3>\n      <div class=\"debug-section\">\n        <h4>Stores 状态</h4>\n        <p>公寓数据加载: {{ apartmentStore.loading ? '加载中...' : '完成' }}</p>\n        <p>是否有数据: {{ apartmentStore.hasData ? '是' : '否' }}</p>\n        <p>楼层数量: {{ apartmentStore.floorCount }}</p>\n        <p>房间总数: {{ apartmentStore.roomCount }}</p>\n        <p>面板可见: {{ uiStore.isPanelVisible ? '是' : '否' }}</p>\n        <p>自动刷新: {{ settingsStore.autoRefreshEnabled ? '启用' : '禁用' }}</p>\n      </div>\n      <div class=\"debug-section\">\n        <h4>操作</h4>\n        <button @click=\"testLoadData\" class=\"ngq-btn ngq-btn-small\">加载数据</button>\n        <button @click=\"testTogglePanel\" class=\"ngq-btn ngq-btn-small\">切换面板</button>\n        <button @click=\"testToggleAutoRefresh\" class=\"ngq-btn ngq-btn-small\">切换自动刷新</button>\n        <button @click=\"showDebugPanel = false\" class=\"ngq-btn ngq-btn-small ngq-btn-secondary\">关闭</button>\n      </div>\n    </div>\n\n    <!-- 显示调试面板按钮 -->\n    <button \n      v-if=\"!showDebugPanel\" \n      @click=\"showDebugPanel = true\" \n      class=\"ngq-debug-toggle\"\n      title=\"显示测试面板\"\n    >\n      🧪\n    </button>\n  </div>\n</template>\n\n<script setup lang=\"ts\">\nimport { ref, onMounted } from 'vue';\nimport ToggleButton from './components/ToggleButton.vue';\nimport { useApartmentStore, useUIStore, useSettingsStore } from './stores';\n\n// Stores\nconst apartmentStore = useApartmentStore();\nconst uiStore = useUIStore();\nconst settingsStore = useSettingsStore();\n\n// 调试面板显示状态\nconst showDebugPanel = ref(false);\n\n/**\n * 测试加载数据\n */\nconst testLoadData = async () => {\n  console.log('🧪 测试：加载数据');\n  const success = await apartmentStore.loadData();\n  if (success) {\n    toastr?.success('数据加载成功');\n  } else {\n    toastr?.error('数据加载失败');\n  }\n};\n\n/**\n * 测试切换面板\n */\nconst testTogglePanel = () => {\n  console.log('🧪 测试：切换面板');\n  uiStore.togglePanel();\n};\n\n/**\n * 测试切换自动刷新\n */\nconst testToggleAutoRefresh = () => {\n  console.log('🧪 测试：切换自动刷新');\n  settingsStore.toggleAutoRefresh();\n};\n\n/**\n * 初始化\n */\nonMounted(() => {\n  console.log('✅ App 组件已挂载');\n  \n  // 初始化设置\n  settingsStore.initialize();\n  \n  // 显示欢迎信息\n  console.log('🏢 网红小区 Vue 版已启动');\n  console.log('💡 提示：点击右下角的 🧪 图标可以打开测试面板');\n});\n</script>\n\n<style scoped>\n.ngq-vue-app {\n  position: relative;\n}\n\n/* 调试面板 */\n.ngq-debug-panel {\n  position: fixed;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  width: 400px;\n  max-width: 90vw;\n  background: var(--apt-bg);\n  border: 2px solid var(--apt-primary);\n  border-radius: var(--apt-radius-lg);\n  padding: 20px;\n  z-index: 10003;\n  color: var(--apt-text);\n  box-shadow: var(--apt-shadow-lg);\n  font-family: 'Microsoft YaHei', 'Segoe UI', sans-serif;\n}\n\n.ngq-debug-panel h3 {\n  margin: 0 0 16px 0;\n  color: var(--apt-primary);\n  font-size: 18px;\n}\n\n.ngq-debug-panel h4 {\n  margin: 12px 0 8px 0;\n  color: var(--apt-text);\n  font-size: 14px;\n  font-weight: 600;\n}\n\n.debug-section {\n  margin-bottom: 16px;\n  padding: 12px;\n  background: rgba(255, 255, 255, 0.05);\n  border-radius: var(--apt-radius-sm);\n}\n\n.debug-section p {\n  margin: 4px 0;\n  font-size: 13px;\n  color: var(--apt-dim);\n}\n\n.debug-section button {\n  margin: 4px 4px 4px 0;\n}\n\n/* 调试面板切换按钮 */\n.ngq-debug-toggle {\n  position: fixed;\n  bottom: 20px;\n  right: 20px;\n  width: 48px;\n  height: 48px;\n  background: var(--apt-gradient-primary);\n  border: 2px solid rgba(255, 255, 255, 0.3);\n  border-radius: 50%;\n  font-size: 24px;\n  cursor: pointer;\n  z-index: 10000;\n  box-shadow: var(--apt-shadow-md);\n  transition: all var(--apt-transition-normal);\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n\n.ngq-debug-toggle:hover {\n  transform: scale(1.1);\n  box-shadow: var(--apt-shadow-lg);\n}\n</style>\n\n"],"sourceRoot":""}]);
